<template>
  <el-col :span="21" class="order-container">
    <div class="form-container">
      <el-col :span="2">
        <el-button type="primary" @click.native.prevent="addBanner">新增</el-button>
      </el-col>
    </div>
    <el-table :data="tableData" border style="width: 100%; margin-top: 20px" :max-height="700">
      <el-table-column prop="date" label="banner名称" header-align="center" align="center"></el-table-column>
      <el-table-column prop="name" label="焦点图" header-align="center" align="center">
        <template>
          <img class="banner-img" src="../../public/img/test.jpeg" alt />
        </template>
      </el-table-column>
      <el-table-column label="操作" header-align="center" align="center">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteBanner(scope.$index, tableData)"
            type="text"
            size="small"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="新增banner" :visible.sync="dialogFormVisible" width="30%">
      <div class="dialog-flex">
        <el-input v-model="serviceName" autocomplete="off" placeholder="请输入banner名称"></el-input>
        <span class="upload-title">上传焦点图：高度xxx * 宽度xxx</span>
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">保 存</el-button>
      </div>
    </el-dialog>
  </el-col>
</template>

<script>
export default {
  name: "ActivityManage",
  data() {
    return {
      imageUrl: "",
      dialogFormVisible: false,
      serviceName: "",
      tableData: [
        {
          date: "2016-05-03",
          name: "王小虎"
        },
        {
          date: "2016-05-03",
          name: "王小虎"
        },
        {
          date: "2016-05-03",
          name: "王小虎"
        },
        {
          date: "2016-05-03",
          name: "王小虎"
        },
        {
          date: "2016-05-03",
          name: "王小虎"
        }
      ]
    };
  },
  created() {},
  watch: {},
  methods: {
    addBanner(index, tableData) {
      console.log("params:", index, tableData);
      this.dialogFormVisible = true;
    },
    deleteBanner(index, tableData) {
      console.log("params:", index, tableData);
      this.$confirm("确定删除banner吗?", "提示", {
        confirmButtonText: "删除",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>
